.verify-number-child {
  width: 375px;
  height: 812px;
  position: relative;
  border-radius: var(--br-11xl);
  background-color: var(--color-white);
  display: none;
  max-width: 100%;
}
.connectivity-03-icon {
  height: 280px;
  width: 280px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 1;
}
.container3 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-xl);
}
.otp-verification {
  position: relative;
  line-height: 16px;
  font-weight: 600;
  z-index: 1;
}
.verification {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 65px;
  font-size: var(--font-size-5xl);
  color: var(--color-black);
}
.enter-the-otp {
  position: relative;
  line-height: 16px;
  z-index: 1;
}
.message-input {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-12xs) 0 0;
}
.phone-number {
  position: relative;
  line-height: 16px;
  font-weight: 600;
  color: var(--color-black);
  display: inline-block;
  min-width: 129px;
  white-space: nowrap;
  z-index: 1;
}
.input-row,
.message {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.input-row {
  flex: 1;
  justify-content: flex-start;
  gap: var(--gap-8xs);
}
.message {
  align-self: stretch;
  justify-content: flex-end;
  padding: 0 var(--padding-2xl) 0 var(--padding-3xl);
}
.button-row-child {
  height: 56px;
  width: 56px;
  position: relative;
  border-bottom: 2px solid var(--color-gainsboro-100);
  box-sizing: border-box;
  display: none;
}
.placeholder1 {
  position: relative;
  line-height: 16px;
  font-weight: 600;
  display: inline-block;
  min-width: 16px;
  z-index: 2;
}
.button-row,
.button-row1 {
  border-bottom: 2px solid var(--color-gainsboro-100);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-xl) var(--padding-xl) var(--padding-mid);
  z-index: 1;
}
.button-row1 {
  padding: var(--padding-xl) var(--padding-lgi) var(--padding-mid)
    var(--padding-2xl);
}
.div62 {
  position: relative;
  line-height: 16px;
  font-weight: 600;
  display: inline-block;
  min-width: 16px;
  z-index: 1;
}
.button-container,
.button-row3 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.button-row3 {
  border-bottom: 2px solid var(--color-gainsboro-100);
  justify-content: flex-start;
  padding: var(--padding-xl) var(--padding-xl) var(--padding-mid);
  z-index: 2;
}
.button-container {
  align-self: stretch;
  justify-content: space-between;
  gap: var(--gap-xl);
  font-size: var(--font-size-5xl);
  color: var(--color-black);
}
.instructions {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 31px;
}
.resend-otp {
  position: relative;
  line-height: 16px;
  color: var(--color-salmon);
  display: inline-block;
  min-width: 82px;
  z-index: 1;
}
.resend,
.resend-container {
  flex-direction: row;
  justify-content: flex-start;
}
.resend-container {
  flex: 1;
  display: flex;
  align-items: flex-start;
  gap: var(--gap-10xs);
}
.resend {
  align-self: stretch;
  padding: 0 var(--padding-23xl);
}
.content1,
.main,
.resend {
  display: flex;
  align-items: flex-start;
}
.content1 {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  gap: 33.7px;
}
.main {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-end;
  padding: 0 var(--padding-10xs) 0 var(--padding-9xs);
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
.divider2 {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-4xl);
  background-color: var(--color-gray-200);
  cursor: pointer;
  z-index: 1;
}
.verify-proceed {
  position: relative;
  line-height: 16px;
  font-weight: 600;
  z-index: 2;
}
.actions1,
.verify-number {
  position: relative;
  display: flex;
}
.actions1 {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-mini) var(--padding-xl) var(--padding-mini)
    var(--padding-2xl);
  white-space: nowrap;
}
.verify-number {
  width: 100%;
  border-radius: var(--br-11xl);
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 109px var(--padding-5xl) 68px;
  box-sizing: border-box;
  gap: 72px;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-white);
  font-family: var(--font-inter);
}
